import { FC, Fragment } from 'react';
import { CosmosCustom } from 'slate';

import { hasProp } from '@pega/cosmos-react-core';

import { nodeRenderer, leafRenderer } from './utils/renderers';

type CosmosNode = CosmosCustom.Element | CosmosCustom.Text;

const getNode = (node: CosmosNode, index: number) => {
  if (hasProp(node, 'children')) {
    const children: JSX.Element[] = node.children.map((childNode, i) => getNode(childNode, i));
    return <Fragment key={index}>{nodeRenderer({ ...node, children })}</Fragment>;
  }
  return <Fragment key={index}>{leafRenderer(node)}</Fragment>;
};

export interface RichTextTreeRendererProps {
  nodes: CosmosNode[];
}

export const RichTextTreeRenderer: FC<RichTextTreeRendererProps> = ({ nodes }) => {
  return <>{nodes.map((node, i) => getNode(node, i))}</>;
};
