import {
  FunctionComponent,
  PropsWithoutRef,
  Ref,
  forwardRef,
  useState,
  ChangeEvent,
  useCallback,
  KeyboardEvent,
  useEffect,
  useRef
} from 'react';

import { ForwardProps, NoChildrenProp, OmitStrict } from '../../types';
import Icon, { registerIcon } from '../Icon';
import * as searchIcon from '../Icon/icons/search.icon';
import { FormControlProps } from '../FormControl';
import {
  useConsolidatedRef,
  useEscape,
  useI18n,
  useOuterEvent,
  useLiveLog,
  useUID
} from '../../hooks';
import Flex from '../Flex';
import Popover from '../Popover';
import { sameWidth } from '../Popover/modifiers';
import Menu, { MenuItemProps } from '../Menu';
import Link, { LinkProps } from '../Link';
import Text from '../Text';
import { createStringMatcher } from '../../utils';
import VisuallyHiddenText from '../VisuallyHiddenText';

import {
  StyledCancelButton,
  StyledResultsPopover,
  StyledSearchButton,
  StyledSearchFilterText,
  StyledSearchInput,
  StyledSearchMenu,
  StyledSearchResultsContainer,
  StyledSearchTextInput,
  StyledMenuButton
} from './SearchInput.styles';

registerIcon(searchIcon);

export type SearchResult = Pick<MenuItemProps, 'id' | 'primary' | 'secondary' | 'href' | 'onClick'>;

export type RecentSearch = Pick<MenuItemProps, 'id' | 'primary' | 'href' | 'onClick'>;

export interface SearchInputProps extends NoChildrenProp {
  /**
   * Placeholder text. The default value is a locale translation of 'Search'
   * @default 'Search'
   */
  placeholder?: FormControlProps['placeholder'];
  /**
   * Creates a controlled input and sets the value. Requires an onChange handler to update value.
   * value + onChange is the recommended method per React team.
   */
  value?: FormControlProps['value'];
  /** Called when the SearchInput value changes. */
  onSearchChange?: (value: string) => void;
  /**
   * Called when the SearchButton is clicked or when the Enter key is pressed while SearchInput is in focus.
   * NOTE: this does not trigger an actual form submit event. It simply represents an explicit action by the user to search as opposed to onSearchChange.
   */
  onSearchSubmit?: (value: string) => void;
  /** A list of user selectable scopes to filter search with. */
  filters?: string[];
  /** The selected scope filter on initial render. */
  defaultFilter?: string;
  /** Called when user changes scope filter. */
  onFilterChange?: (value: string) => void;
  /** A list of matching results presented in the input's dropdown menu. */
  searchResults?: SearchResult[];
  /** A list of recent searches presented in the input's dropdown menu. */
  recentSearches?: RecentSearch[];
  /** Places the menu into an indeterminate loading state. */
  loading?: boolean;
  /** Prop passed to the advanced search page link. */
  advancedSearchLink?: OmitStrict<LinkProps, 'children'>;
  /** Aria label for search input, used by screen readers. */
  searchInputAriaLabel?: string;
  /**
   * Boolean for determining for whether the search results will render in a popover.
   * @default true
   */
  resultsPopover?: boolean;
}

const SearchInput: FunctionComponent<SearchInputProps & ForwardProps> = forwardRef(
  function SearchInput(props: PropsWithoutRef<SearchInputProps>, ref: Ref<HTMLInputElement>) {
    const t = useI18n();

    const {
      value = '',
      defaultFilter,
      filters,
      onFilterChange,
      searchResults,
      recentSearches,
      loading: loadingProp,
      advancedSearchLink,
      placeholder = t('search_placeholder_default'),
      onSearchChange,
      onSearchSubmit,
      searchInputAriaLabel,
      resultsPopover = true,
      ...restProps
    } = props;
    const instructionTextId = useUID();
    const { announcePolite } = useLiveLog();
    const inputRef = useConsolidatedRef(ref);
    const filterButtonRef = useRef<HTMLButtonElement>(null);
    const filtersRef = useRef<HTMLDivElement>(null);
    const searchResultsRef = useRef<HTMLDivElement>(null);

    const [searchMenuOpen, setSearchMenuOpen] = useState(false);
    const [searchResultItems, setSearchResultItems] = useState<MenuItemProps[] | undefined>(
      undefined
    );
    const [selectedFilter, setSelectedFilter] = useState(defaultFilter ?? '');
    const [downPressed, setDownPressed] = useState(false);

    const completeFilters = [t('all'), ...(filters ?? [])];
    const hasFilters = completeFilters.length > 1;
    const hasSearchResults = !!(searchResults && searchResults.length);
    const hasRecentSearches = !!(recentSearches && recentSearches.length);
    const showSearchMenu = (value || recentSearches?.length) && (searchResultItems || loadingProp);
    const useRecents = hasRecentSearches && !value;
    const loading = useRecents ? false : !!loadingProp;
    const [selectedFilterValue, setSelectedFilterValue] = useState('All');
    const searchMenuVisible =
      (resultsPopover && showSearchMenu && searchMenuOpen) || (!resultsPopover && showSearchMenu);

    useEscape(e => {
      if (searchMenuOpen) {
        e.preventDefault();
        e.stopPropagation();
        setSearchMenuOpen(false);
      }
    });

    const onKeyDown = useCallback(
      (e: KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter' && !downPressed) {
          onSearchSubmit?.(value);
        }
        if (e.key === 'ArrowDown') {
          setDownPressed(true);
        }
      },
      [onSearchSubmit, downPressed, value]
    );

    useOuterEvent('click', [searchResultsRef, inputRef, filtersRef], () => {
      setSearchMenuOpen(false);
    });

    useEffect(() => {
      setDownPressed(false);
    }, [value]);

    useEffect(() => {
      let items: MenuItemProps[] | undefined = [];
      if (hasSearchResults && value) {
        items = searchResults;
      } else if (useRecents) {
        items = recentSearches;
      } else if (!searchResults && (!recentSearches || recentSearches.length === 0)) {
        items = undefined;
      }
      setSearchResultItems(items);
    }, [searchResults, recentSearches, searchMenuOpen, hasSearchResults, hasRecentSearches, value]);

    // Announce results
    useEffect(() => {
      if (!searchMenuVisible || loading) return;

      const timeout = setTimeout(() => {
        announcePolite({
          message: t(
            useRecents ? 'recent_results_count' : 'results_count',
            [searchResultItems?.length ?? 0],
            {
              count: searchResultItems?.length ?? 0
            }
          ),
          type: 'status'
        });
      }, 1000);

      return () => {
        clearTimeout(timeout);
      };
    }, [loading, searchMenuVisible, searchResultItems]);

    const searchMenu = (
      <Menu
        as={StyledSearchMenu}
        header={
          recentSearches && !value ? (
            <Text variant='secondary'>{t('recent_searches')}</Text>
          ) : undefined
        }
        mode='action'
        items={searchResultItems ?? []}
        loading={loading}
        footer={
          advancedSearchLink ? (
            <Link {...advancedSearchLink}>{t('advanced_search')}</Link>
          ) : undefined
        }
        accent={hasSearchResults ? createStringMatcher(value, undefined, 'gi') : undefined}
        focusControlEl={(downPressed && inputRef.current) || undefined}
        aria-label={searchInputAriaLabel || t('search')}
      />
    );
    return (
      <>
        <Flex container as={StyledSearchInput} hasFilters={hasFilters}>
          {hasFilters ? (
            <StyledMenuButton
              text={selectedFilterValue}
              variant='text'
              icon='search'
              aria-label={
                selectedFilterValue === 'All'
                  ? t('select_search_filter')
                  : t('selected_search_filter', [selectedFilterValue])
              }
              menu={{
                mode: 'single-select',
                items: completeFilters.map(filter => ({
                  primary: filter,
                  id: filter,
                  selected: filter === t('all') ? selectedFilter === '' : selectedFilter === filter,
                  onClick: () => {
                    setSelectedFilterValue(filter);
                    setSelectedFilter(filter === t('all') ? '' : filter);
                    onFilterChange?.(filter);
                    inputRef.current?.focus();
                  }
                }))
              }}
            />
          ) : (
            <Flex
              as={StyledSearchButton}
              ref={filterButtonRef}
              forwardedAs={hasFilters ? undefined : 'div'}
              container={{
                justify: 'center',
                alignItems: 'center',
                gap: selectedFilter ? 0.5 : undefined
              }}
              decoupled={hasFilters}
              aria-label={hasFilters ? t('select_search_filter') : undefined}
            >
              <Icon name='search' />
              {selectedFilter && <StyledSearchFilterText>{selectedFilter}</StyledSearchFilterText>}
              {hasFilters && <Icon name='arrow-micro-down' />}
            </Flex>
          )}

          <StyledSearchTextInput
            ref={inputRef}
            type='search'
            aria-label={searchInputAriaLabel}
            placeholder={placeholder}
            {...restProps}
            aria-describedby={searchMenuVisible ? instructionTextId : undefined}
            value={value}
            onChange={(e: ChangeEvent<HTMLInputElement>) => {
              onSearchChange?.(e.target.value);
              if (e.target.value) setSearchMenuOpen(true);
            }}
            onKeyDown={onKeyDown}
            onFocus={() => {
              setSearchMenuOpen(true);
            }}
            decoupled={hasFilters}
            onBlur={() => {
              setDownPressed(false);
              if (!advancedSearchLink) {
                setSearchMenuOpen(false);
              }
            }}
            autoComplete='off'
          />
          {value && (
            <StyledCancelButton
              icon
              onClick={() => {
                onSearchChange?.('');
                inputRef.current?.focus();
              }}
              variant='simple'
              compact
              label={t('clear')}
            >
              <Icon name='times' />
            </StyledCancelButton>
          )}
        </Flex>
        {showSearchMenu && resultsPopover && (
          <Popover
            as={StyledResultsPopover}
            target={inputRef.current}
            show={searchMenuOpen}
            placement='bottom-start'
            modifiers={[sameWidth]}
            ref={searchResultsRef}
          >
            {searchMenu}
          </Popover>
        )}
        {!resultsPopover && showSearchMenu && (
          <StyledSearchResultsContainer>{searchMenu}</StyledSearchResultsContainer>
        )}
        {searchMenuVisible && (
          <VisuallyHiddenText id={instructionTextId}>
            {`${t('search_instructions')} `}
          </VisuallyHiddenText>
        )}
      </>
    );
  }
);

export default SearchInput;
