import { remToPx, stripUnit } from 'polished';
import { FunctionComponent, CSSProperties, useEffect, useState } from 'react';
import styled, { css } from 'styled-components';

import { useElement, useTheme } from '../../hooks';
import { calculateFontSize, FontSize } from '../../styles';
import { defaultThemeProp } from '../../theme';
import { ForwardProps } from '../../types';
import { tryCatch } from '../../utils';
import Flex from '../Flex';
import Text from '../Text';
import Tooltip from '../Tooltip';

import { CurrentStepPopover, StepMarker, StyledBar, StyledFill } from './FormProgress.styles';
import type MultiStepFormProps from './MultiStepForm.types';

interface FormProgressProps {
  steps: MultiStepFormProps['steps'];
  currentStepId: MultiStepFormProps['currentStepId'];
}

export const StyledFormProgress = styled(Flex)(({ theme }) => {
  const {
    base: { spacing, 'font-size': fontSize, 'font-scale': fontScale },
    components: {
      text: {
        h4: { 'font-size': h4fontSize }
      }
    }
  } = theme;
  const fontSizes = calculateFontSize(fontSize, fontScale);

  return css`
    position: relative;
    padding-block-start: calc(${spacing} + ${fontSizes[h4fontSize as FontSize]} * 1.35);
  `;
});

StyledFormProgress.defaultProps = defaultThemeProp;

const HorizontalFormProgress: FunctionComponent<FormProgressProps & ForwardProps> = ({
  steps,
  currentStepId,
  ...restProps
}) => {
  const curIdx = steps.findIndex(step => step.id === currentStepId);
  const curStep = steps[curIdx];
  const fillScale = curIdx / (steps.length - 1);

  const [currentStepEl, setCurrentStepEl] = useElement();
  const [stepEls, setStepEls] = useState<Record<string, HTMLElement>>({});
  const theme = useTheme();

  let popoverPlacement: 'top' | 'top-start' | 'top-end' = 'top';
  if (curIdx === 0) popoverPlacement = 'top-start';
  else if (curIdx === steps.length - 1) popoverPlacement = 'top-end';

  useEffect(() => {
    setCurrentStepEl(stepEls[curStep.id]);
  }, [Object.keys(stepEls), curStep]);

  return (
    <StyledFormProgress
      {...restProps}
      container={{ alignItems: 'center' }}
      role='progressbar'
      aria-valuenow={curIdx}
      aria-valuemin={0}
      aria-valuetext={curStep.name}
      aria-valuemax={steps.length}
    >
      <CurrentStepPopover
        show
        target={currentStepEl}
        placement={popoverPlacement}
        strategy='absolute'
        portal={false}
        hideOnTargetHidden
        modifiers={[
          {
            name: 'offset',
            options: {
              offset: [
                0,
                tryCatch(
                  () => stripUnit(remToPx(theme.base.spacing)),
                  () => 8
                )
              ]
            }
          },
          {
            name: 'placeAndContain',
            enabled: false
          },
          {
            name: 'flip',
            enabled: false
          },
          {
            name: 'preventOverflow',
            options: {
              padding: 0
            }
          }
        ]}
      >
        <Text variant='h4'>{curStep.name}</Text>
      </CurrentStepPopover>
      <Flex container={{ justify: 'between', alignItems: 'center' }} item={{ grow: 1 }}>
        {steps.map((step, index) => {
          return (
            <StepMarker
              key={step.id}
              ref={(el: HTMLElement | null) => {
                setStepEls(curr => {
                  const newEls = { ...curr };
                  if (!el && newEls[step.id]) {
                    delete newEls[step.id];
                    return newEls;
                  }
                  if (el) return { ...newEls, [step.id]: el };
                  return newEls;
                });
              }}
              aria-label={step.name}
              aria-describedby={step.id}
              current={index === curIdx}
              depth={step.depth}
              prior={index < curIdx}
              onClick={step.onMarkerClick}
            >
              {index !== curIdx && (
                <Tooltip
                  target={stepEls[step.id]}
                  showDelay='none'
                  hideDelay='short'
                  portal={false}
                >
                  {step.name}
                </Tooltip>
              )}
            </StepMarker>
          );
        })}
      </Flex>

      <StyledBar>
        <StyledFill style={{ '--fillScale': fillScale } as CSSProperties} />
      </StyledBar>
    </StyledFormProgress>
  );
};

export default HorizontalFormProgress;
