import {
  forwardRef,
  FunctionComponent,
  MutableRefObject,
  PropsWithoutRef,
  useContext,
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
  useMemo,
  Fragment,
  useCallback,
  MouseEvent
} from 'react';

import { ForwardProps } from '../../types';
import Drawer from '../Drawer';
import {
  useAfterInitialEffect,
  useConsolidatedRef,
  useItemIntersection,
  useI18n,
  useDirection,
  useLiveLog
} from '../../hooks';
import Progress from '../Progress';
import EmptyState from '../EmptyState';

import type {
  AcceptedMouseEventElement,
  MenuListProps,
  ParentMenuItemProps,
  MenuItemProps
} from './Menu.types';
import MenuContext from './Menu.context';
import MenuListHeader from './MenuListHeader';
import MenuItem from './MenuItem';
import helpers from './helpers';
import {
  StyledLoadingItem,
  StyledMenuList,
  StyledMenuListContainer,
  StyledSeparator
} from './Menu.styles';
import { resizeRootEl } from './NavItemsList';
import MenuGroup from './MenuGroup';

const setParentDisabled = (fieldset: HTMLFieldSetElement, bool: boolean) => {
  fieldset.disabled = bool;
  const legendButton = fieldset.querySelector<HTMLButtonElement>('legend button');
  if (legendButton) {
    legendButton.disabled = bool;
  }
};

const countVisibleItems = (menuItems: MenuListProps['items']): number => {
  return menuItems.reduce<number>((acc, item) => {
    const isFlatGroup = !helpers.isItem(item);
    if (isFlatGroup && item.items) {
      return acc + item.items.length + 1;
    }

    return acc + 1;
  }, 0);
};

const MenuList: FunctionComponent<MenuListProps & ForwardProps> = forwardRef(
  function MenuListFunction(
    { items, parent, id, menuRole, ...restProps }: PropsWithoutRef<MenuListProps>,
    ref: MenuListProps['ref']
  ) {
    const t = useI18n();
    const menuListWrapperRef: MutableRefObject<HTMLElement | null> = useRef(null);
    const selfRef = useConsolidatedRef(ref);
    const ulRef = useRef<HTMLUListElement>(null);
    const returnFocusRef: MutableRefObject<HTMLButtonElement | null> = useRef(null);
    const [open, setOpen] = useState(!parent);
    const [expandedItem, setExpandedItem] = useState<ParentMenuItemProps>();
    const {
      scrollAt,
      'aria-label': label,
      loadMore,
      loading,
      emptyText,
      currentItemId,
      onItemExpand,
      focusControl,
      updateActiveDescendants,
      setFocusReturnEl,
      onItemCollapse
    } = useContext(MenuContext);
    const { end } = useDirection();
    const { announcePolite } = useLiveLog();

    useItemIntersection(
      ulRef,
      items.length - 1,
      () => {
        if (!loading) loadMore?.(parent?.item?.id);
      },
      ':scope > li'
    );

    useLayoutEffect(() => {
      if (!selfRef.current || expandedItem) return;
      menuListWrapperRef.current = selfRef.current.parentElement;
      resizeRootEl(
        selfRef.current,
        menuListWrapperRef.current as HTMLElement,
        countVisibleItems(items),
        scrollAt
      );
    });

    useEffect(() => {
      if (parent) setOpen(true);
    }, []);

    useAfterInitialEffect(() => {
      if (expandedItem) {
        setExpandedItem(items.find(item => item.id === expandedItem.id) as ParentMenuItemProps); // FIXME
      }
    }, [expandedItem, items]);

    useAfterInitialEffect(() => {
      if (currentItemId) {
        // if there is ancestor item of the controlled item, set it to expanded.
        const ancestor = items.find(
          item => item.items?.length && helpers.getItem(item.items, currentItemId) !== undefined
        );

        if (ancestor) {
          setExpandedItem(ancestor as ParentMenuItemProps);
          updateActiveDescendants({ preventScroll: true });
        }
      }
    }, [currentItemId, items, parent]);

    const onExpandCallback = useCallback(
      (itemId: MenuItemProps['id'], e: MouseEvent<AcceptedMouseEventElement>) => {
        const item = helpers.getItem(items, itemId);
        returnFocusRef.current = e.currentTarget as HTMLButtonElement;
        setExpandedItem(item as ParentMenuItemProps); // FIXME
        item?.onExpand?.(itemId, e);
        onItemExpand?.(itemId, e);
        updateActiveDescendants({ preventScroll: true });
      },
      [items]
    );

    useEffect(() => {
      if (items.length === 0 && !loading) {
        announcePolite({ message: emptyText ?? t('no_items') });
      }
    }, [items.length, loading]);

    const listContent = useMemo(() => {
      if (items.length) {
        return items.map((item, index) => {
          return helpers.isItem(item) ? (
            <MenuItem
              {...item}
              key={item.id}
              role={menuRole === 'listbox' ? 'option' : 'menuitem'}
              onExpand={item.items ? onExpandCallback : undefined}
            />
          ) : (
            <Fragment key={item.id}>
              <MenuGroup {...item} itemRole={menuRole === 'listbox' ? 'option' : 'menuitem'} />
              {items[index + 1] && helpers.isItem(items[index + 1]) && (
                <StyledSeparator role='separator' />
              )}
            </Fragment>
          );
        });
      }

      if (!loading) {
        return <EmptyState message={emptyText} forwardedAs='li' />;
      }

      return null;
    }, [items, loading, emptyText]);

    const list = (
      <StyledMenuList id={expandedItem ? undefined : id} ref={ulRef} role={menuRole} {...restProps}>
        {parent && (
          <MenuListHeader
            text={parent.item.primary}
            onClick={() => {
              setOpen(false);
            }}
          />
        )}
        {listContent}
        {loading && !expandedItem && (
          <StyledLoadingItem>
            <Progress placement='local' liveConfig={{ contextualLabel: label ?? t('menu') }} />
          </StyledLoadingItem>
        )}
      </StyledMenuList>
    );

    return (
      <>
        <Drawer
          aria-hidden={!!expandedItem}
          as={StyledMenuListContainer}
          ref={selfRef}
          open={open}
          style={{ opacity: expandedItem ? 0 : 1 }}
          disabled={!!expandedItem}
          placement={end}
          onBeforeOpen={() => {
            if (parent) {
              parent.el.style.opacity = '0';
            }

            if (selfRef.current) {
              selfRef.current.style.opacity = '1';
            }
          }}
          onAfterOpen={() => {
            if (parent) {
              setParentDisabled(parent.el, true);
            }
          }}
          onBeforeClose={() => {
            if (parent && menuListWrapperRef.current) {
              setParentDisabled(parent.el, false);
              parent.el.style.opacity = '1';
              resizeRootEl(
                parent.el,
                menuListWrapperRef.current,
                countVisibleItems(parent.siblingItems),
                scrollAt
              );
            }

            if (selfRef.current) {
              selfRef.current.style.opacity = '0';
            }
          }}
          onAfterClose={() => {
            if (parent) {
              focusControl?.focus();

              const expandBtn = parent.returnFocusRef.current;
              parent.setExpandedItem(undefined);

              if (expandBtn) {
                setFocusReturnEl(expandBtn?.closest('li'));
              }

              onItemCollapse?.(parent.item.id);
              updateActiveDescendants();
            }
          }}
        >
          {list}
        </Drawer>

        {expandedItem && selfRef.current && (
          <MenuList
            id={id}
            items={expandedItem.items}
            parent={{
              el: selfRef.current,
              item: expandedItem,
              siblingItems: items,
              setExpandedItem,
              returnFocusRef
            }}
            menuRole={menuRole}
          />
        )}
      </>
    );
  }
);

export default MenuList;
