/**
 * Checks if two URLs have the same origin.
 * @see {@link https://developer.mozilla.org/en-US/docs/Web/Security/Same-origin_policy#definition_of_an_origin Definition of an origin}
 */
const sameOrigin = (
  {
    protocol: protocolA,
    hostname: hostnameA,
    port: portA
  }: Pick<URL, 'protocol' | 'hostname' | 'port'>,
  {
    protocol: protocolB,
    hostname: hostnameB,
    port: portB
  }: Pick<URL, 'protocol' | 'hostname' | 'port'>
): boolean => {
  if (protocolA !== protocolB) return false;
  if (hostnameA !== hostnameB) return false;

  if (!portA) {
    if (protocolA === 'http:') portA = '80';
    else if (protocolA === 'https:') portA = '443';
  }

  if (!portB) {
    if (protocolB === 'http:') portB = '80';
    else if (protocolB === 'https:') portB = '443';
  }

  return portA === portB;
};

export default sameOrigin;
