import {
  forwardRef,
  FunctionComponent,
  PropsWithoutRef,
  useCallback,
  useEffect,
  useState,
  useRef,
  useMemo,
  MouseEvent
} from 'react';

import {
  useConsolidatedRef,
  useUID,
  useActiveDescendant,
  useLazyDescendant,
  useI18n,
  usePrevious,
  useDirection,
  useTestIds
} from '../../hooks';
import { ForwardProps } from '../../types';
import { cap, withTestIds } from '../../utils';
import VisuallyHiddenText from '../VisuallyHiddenText';
import Button from '../Button/Button';
import Icon from '../Icon/Icon';

import FlyoutMenuList from './FlyoutMenuList';
import MenuContext from './Menu.context';
import { StyledMenu, StyledMenuListWrapper } from './Menu.styles';
import { getMenuTestIds } from './Menu.test-ids';
import {
  MenuContextProps,
  MenuProps,
  MenuItemProps,
  AcceptedMouseEventElement
} from './Menu.types';
import MenuList from './MenuList';

const Menu: FunctionComponent<MenuProps & ForwardProps> = forwardRef(function Menu(
  props: PropsWithoutRef<MenuProps>,
  ref: MenuProps['ref']
) {
  const uid = useUID();
  const {
    testId,
    id = uid,
    items = [],
    onCreateNew,
    mode = 'action',
    accent,
    scrollAt = 7,
    emptyText,
    onItemClick,
    onItemActive,
    onItemExpand,
    loadMore,
    onItemCollapse,
    loading = false,
    currentItemId,
    header,
    footer,
    variant = 'drill-down',
    focusControlEl,
    arrowNavigationUnsupported,
    'aria-describedby': ariaDescribedBy,
    'aria-label': label,
    role = 'menu',
    listId,
    pauseDescendantEvaluation,
    ...restProps
  } = props;

  const testIds = useTestIds(testId, getMenuTestIds);

  const t = useI18n();

  const { end: endDirection, start: startDirection } = useDirection();
  const radioName = useUID();
  const menuRef = useConsolidatedRef(ref);
  const previousItemCount = useRef(0);
  const [activeDescendantUpdateId, setActiveDescendantUpdateId] = useState(0);
  const [focusDescendant, setFocusDescendant] = useState<HTMLElement | null>(null);
  const [flyOutActiveIdStack, setFlyoutActiveIdStack] = useState<string[]>([id]);
  const [focusReturnEl, setFocusReturnEl] = useState<HTMLElement | null>();
  const [preventInitialScroll, setPreventInitialScroll] = useState(false);
  const [flyoutFocusReturnStack, setFlyoutFocusReturnStack] = useState<(HTMLElement | null)[]>([]);

  const menuAsFocusControl = !focusControlEl;

  const activeFlyoutSelector = useMemo(
    () => `fieldset[data-flyout-menu-id="${flyOutActiveIdStack[flyOutActiveIdStack.length - 1]}"]`,
    [flyOutActiveIdStack]
  );

  const focusControl = useMemo(() => {
    return focusControlEl || menuRef.current;
  }, [focusControlEl, menuRef.current]);

  const pushFlyoutId = useCallback(
    (flyoutId: string) => {
      setFlyoutActiveIdStack([...flyOutActiveIdStack, flyoutId]);
    },
    [flyOutActiveIdStack]
  );

  useEffect(() => {
    setFlyoutActiveIdStack([...flyOutActiveIdStack, id]);
  }, [id]);

  const updateParentDescendantStack = useCallback(
    (item: HTMLElement | null) => {
      setFlyoutFocusReturnStack([...flyoutFocusReturnStack, item]);
    },
    [flyoutFocusReturnStack]
  );

  const getScopedItemId = useCallback(
    (itemId: string) => {
      return `${id}-item-${itemId}`;
    },
    [id]
  );

  const getUnscopedItemId = useCallback(
    (itemId: string) => {
      return itemId.split(`${id}-item-`)[1];
    },
    [id]
  );

  const updateActiveDescendants: MenuContextProps['updateActiveDescendants'] = useCallback(
    ({ preventScroll = false } = {}) => {
      setPreventInitialScroll(preventScroll);
      setActiveDescendantUpdateId(Math.random());
    },
    []
  );

  const uadConfig = useMemo(
    () => ({
      focusEl: focusControl,
      scope: menuRef.current,
      scopeSelector:
        variant === 'drill-down'
          ? ('fieldset[aria-hidden="false"]' as keyof HTMLElementTagNameMap)
          : activeFlyoutSelector,
      selector: `[role="${
        role === 'menu' ? 'menuitem' : 'option'
      }"], legend` as keyof HTMLElementTagNameMap,
      focusDescendantEl: focusDescendant,
      clearFocusDescendant: () => {
        setFocusDescendant(null);
      },
      focusReturnEl,
      clearFocusReturn: () => {
        setFocusReturnEl(null);
      },
      currentDescendantId: currentItemId ? getScopedItemId(currentItemId) : undefined,
      preventInitialScroll,
      clearPreventScroll: () => {
        setPreventInitialScroll(false);
      },
      pauseDescendantEvaluation
    }),
    [
      focusControl,
      menuRef.current,
      activeFlyoutSelector,
      focusReturnEl,
      focusDescendant,
      currentItemId,
      preventInitialScroll,
      pauseDescendantEvaluation
    ]
  );

  const { activeDescendant, descendants } = useActiveDescendant(uadConfig, [
    activeDescendantUpdateId
  ]);

  const previousActiveDescendant = usePrevious(activeDescendant);

  useLazyDescendant({
    loading,
    descendants,
    previousActiveDescendant,
    activeDescendant,
    focusReturnEl,
    setFocusReturnEl,
    scrollEl: uadConfig.scope?.querySelector(uadConfig.scopeSelector)?.querySelector('ul')
  });

  // ## Bind Menu-specific navigation keyDown.
  useEffect(() => {
    const expandOrCollapse: (action?: 'collapse' | 'expand' | null) => void = (action = null) => {
      if (activeDescendant) {
        // expand
        if (activeDescendant.dataset.expand === 'true' && action !== 'collapse') {
          activeDescendant.querySelector<HTMLSpanElement>('span[aria-hidden="true"]')?.click();
          return;
        }

        // collapse
        if (action !== 'expand') {
          if (
            variant === 'flyout' &&
            flyOutActiveIdStack.length > 1 &&
            flyoutFocusReturnStack.length > 0
          ) {
            setFlyoutActiveIdStack([...flyOutActiveIdStack].slice(0, -1));

            const parentDescendant = flyoutFocusReturnStack.pop();
            if (parentDescendant !== undefined) setFocusReturnEl(parentDescendant);
            updateActiveDescendants();
          } else if (activeDescendant.dataset.collapse === 'true') activeDescendant.click();
        }
      }
    };

    const additionalKeydown = (e: KeyboardEvent) => {
      switch (e.key) {
        case `Arrow${cap(endDirection)}`: {
          if (arrowNavigationUnsupported) break;
          expandOrCollapse('expand');
          break;
        }
        case `Arrow${cap(startDirection)}`: {
          if (arrowNavigationUnsupported) break;
          expandOrCollapse('collapse');
          break;
        }
        case 'Escape': {
          if (
            variant === 'flyout' &&
            flyOutActiveIdStack.length > 1 &&
            flyoutFocusReturnStack.length > 0
          ) {
            e.preventDefault();
            e.stopPropagation();

            setFlyoutActiveIdStack([...flyOutActiveIdStack].slice(0, -1));
            const parentDescendant = flyoutFocusReturnStack.pop();
            if (parentDescendant !== undefined) setFocusReturnEl(parentDescendant);
            updateActiveDescendants();
          }
          break;
        }
        default:
          break;
      }

      if (arrowNavigationUnsupported && (e.key === ' ' || e.key === 'Spacebar') && e.shiftKey) {
        e.preventDefault();
        expandOrCollapse();
      }
    };

    if (activeDescendant) onItemActive?.(getUnscopedItemId(activeDescendant.id));

    focusControl?.addEventListener('keydown', additionalKeydown);

    return () => focusControl?.removeEventListener('keydown', additionalKeydown);
  }, [focusControl, activeDescendant, flyOutActiveIdStack]);

  // ## Update useActiveDescendant on change of items, not selection of items.
  useEffect(() => {
    // Next tick for DOM updates.
    const timeoutId = setTimeout(() => {
      if (items.length === previousItemCount.current) {
        const previousDescendantIds = descendants?.map(node => node.id);
        const newDescendants = uadConfig.scope
          ?.querySelector(uadConfig.scopeSelector)
          ?.querySelectorAll(uadConfig.selector);

        if (!newDescendants) {
          updateActiveDescendants();
          return;
        }

        const scopedDescendants = Array.from(newDescendants);

        if (scopedDescendants?.length !== previousDescendantIds?.length) {
          updateActiveDescendants();
          return;
        }

        const itemsUnchanged = scopedDescendants.every(
          (node, index) => node.id === previousDescendantIds[index]
        );

        if (!itemsUnchanged) {
          updateActiveDescendants();
        }

        return;
      }

      previousItemCount.current = items.length;
      updateActiveDescendants();
    }, 0);

    return () => clearTimeout(timeoutId);
  }, [items]);

  const contextValue = useMemo(() => {
    return {
      componentId: id,
      'aria-label': label,
      mode,
      arrowNavigationUnsupported,
      onItemClick: (
        clickedId: MenuItemProps['id'],
        event: MouseEvent<AcceptedMouseEventElement>
      ) => {
        if (menuAsFocusControl) {
          menuRef.current?.focus();
        }

        onItemClick?.(clickedId, event);
      },
      onItemActive,
      onItemExpand,
      onItemCollapse,
      accent,
      scrollAt,
      emptyText,
      radioName,
      loadMore,
      loading,
      variant,
      focusControl,
      updateActiveDescendants,
      setFocusReturnEl,
      setFocusDescendant,
      getScopedItemId,
      pushFlyoutId,
      flyOutActiveIdStack,
      updateParentDescendantStack
    };
  }, [
    id,
    label,
    mode,
    arrowNavigationUnsupported,
    onItemClick,
    onItemActive,
    onItemExpand,
    onItemCollapse,
    accent,
    scrollAt,
    emptyText,
    radioName,
    loadMore,
    loading,
    variant,
    focusControl,
    updateActiveDescendants,
    setFocusReturnEl,
    setFocusDescendant,
    getScopedItemId,
    pushFlyoutId,
    flyOutActiveIdStack,
    updateParentDescendantStack
  ]);

  return (
    <StyledMenu
      data-testid={testIds.root}
      id={id}
      aria-describedby={menuAsFocusControl ? `${id}-menuDescription` : undefined}
      {...restProps}
      ref={menuRef}
      tabIndex={menuAsFocusControl ? 0 : undefined}
    >
      {menuAsFocusControl && (
        <span id={`${id}-menuDescription`} hidden>
          {(`${t('menu_selection_instructions')} ` && ariaDescribedBy) || ''}
        </span>
      )}
      {label && <VisuallyHiddenText id={`${id}-menuLabel`}>{label}</VisuallyHiddenText>}

      {header && <header>{header}</header>}

      <StyledMenuListWrapper>
        <MenuContext.Provider value={contextValue}>
          {variant === 'drill-down' ? (
            <MenuList
              items={items}
              id={listId}
              menuRole={role}
              aria-labelledby={label ? `${id}-menuLabel` : undefined}
            />
          ) : (
            <FlyoutMenuList items={items} menuRole={role} />
          )}
        </MenuContext.Provider>
      </StyledMenuListWrapper>
      {(onCreateNew || footer) && (
        <footer>
          {onCreateNew ? (
            <Button data-testid={testIds.createNew} variant='link' onClick={onCreateNew}>
              <Icon name='plus' /> {t('create_new')}
            </Button>
          ) : (
            footer
          )}
        </footer>
      )}
    </StyledMenu>
  );
});

export default withTestIds(Menu, getMenuTestIds);
