import { forwardRef, FunctionComponent, PropsWithoutRef, useRef, useEffect } from 'react';
import styled, { css } from 'styled-components';

import {
  useAfterInitialEffect,
  useUID,
  useI18n,
  usePrevious,
  useBreakpoint,
  useTheme
} from '../../hooks';
import { ForwardProps } from '../../types';
import { getFocusables } from '../../utils';
import { calculateFontSize } from '../../styles';
import Flex from '../Flex';
import Text from '../Text';
import HTML from '../HTML';
import VisuallyHiddenText from '../VisuallyHiddenText';
import Grid, { StyledGrid } from '../Grid';
import { defaultThemeProp } from '../../theme';
import Progress from '../Progress';

import MultiStepFormProps from './MultiStepForm.types';
import VerticalFormProgress from './VerticalFormProgress';
import HorizontalFormProgress from './HorizontalFormProgress';

export const StyledForm = styled.form<{ actions?: boolean; heading?: boolean }>(
  ({ actions, heading, theme }) => {
    return css`
      & > ${StyledGrid} {
        position: relative;

        ${actions &&
        css`
          padding-block-end: calc(2 * ${theme.base.spacing});
        `}

        ${heading &&
        css`
          padding-block-start: calc(2 * ${theme.base.spacing});
        `}
      }
    `;
  }
);

StyledForm.defaultProps = defaultThemeProp;

export const StyledFormContent = styled.div``;

export const StyledRequiredFieldLegend = styled(Text)(({ theme }) => {
  const {
    base: {
      'font-size': fontSize,
      'font-scale': fontScale,
      palette: { urgent }
    }
  } = theme;
  const { xxs: infoFontSize } = calculateFontSize(fontSize, fontScale);

  return css`
    font-size: ${infoFontSize};
    font-style: italic;

    &::before {
      display: 'inline';
      content: '\\00a0*';
      vertical-align: top;
      color: ${urgent};
    }
  `;
});

StyledRequiredFieldLegend.defaultProps = defaultThemeProp;

export const MultiStepForm: FunctionComponent<MultiStepFormProps & ForwardProps> = forwardRef(
  function MultiStepForm(
    {
      currentStepId,
      steps,
      heading,
      stepIndicator: stepIndicatorProp = steps.length < 2 ? 'none' : 'horizontal',
      progress,
      ...restProps
    }: PropsWithoutRef<MultiStepFormProps>,
    ref: MultiStepFormProps['ref']
  ) {
    const multiStepFormContentRef = useRef(null);

    const previousId = usePrevious(currentStepId);
    const currentStep = steps.find(step => step.id === currentStepId)!;
    const headingId = useUID();
    const descriptionId = useUID();
    const t = useI18n();
    const {
      base: { 'content-width': contentWidth }
    } = useTheme();

    const setFocus = () => {
      if (progress) return;

      const focusables = getFocusables(multiStepFormContentRef);
      focusables[0]?.focus();
    };

    useEffect(() => {
      if (previousId === undefined) return;

      if (previousId !== currentStepId) {
        const focusableElements = getFocusables(multiStepFormContentRef);

        if (focusableElements.length) focusableElements[0].focus();
      }
    }, [currentStepId, previousId]);

    useAfterInitialEffect(() => {
      if (!progress) {
        setFocus();
      }
    }, [progress]);

    const isSmallOrAbove = useBreakpoint('sm');

    const stepIndicator =
      stepIndicatorProp === 'vertical' && !isSmallOrAbove ? 'horizontal' : stepIndicatorProp;

    let areaDef = '';
    switch (stepIndicator) {
      case 'horizontal':
        areaDef += '"stepIndicator" ';
        if (currentStep.banners) areaDef += '"banners" ';
        if (currentStep.description) areaDef += '"description" ';
        areaDef += '"content"';
        break;
      case 'vertical':
        if (currentStep.banners) areaDef += '"banners stepIndicator" ';
        if (currentStep.description) areaDef += '"description stepIndicator" ';
        areaDef += '"content stepIndicator"';
        break;
      default:
        if (currentStep.banners) areaDef += '"banners" ';
        if (currentStep.description) areaDef += '"description" ';
        areaDef += '"content"';
    }

    return (
      <StyledForm
        ref={ref}
        {...restProps}
        aria-labelledby={heading ? headingId : undefined}
        aria-describedby={currentStep.description ? descriptionId : undefined}
        heading={!!heading}
        actions={!!currentStep.actions}
      >
        {heading && (
          <Text id={headingId} variant='h3'>
            {heading}
          </Text>
        )}

        <Grid
          container={{
            cols:
              stepIndicator === 'vertical'
                ? `2fr minmax(${contentWidth.xs}, 1fr)`
                : 'minmax(0, 1fr)',
            areas: areaDef,
            gap: 2
          }}
          inert={progress ? '' : undefined}
        >
          {steps.length > 1 && (
            <Grid item={{ area: 'stepIndicator' }}>
              {stepIndicator === 'horizontal' && (
                <Grid
                  item={{ area: 'stepIndicator' }}
                  as={HorizontalFormProgress}
                  steps={steps}
                  currentStepId={currentStepId}
                />
              )}
              {stepIndicator === 'vertical' && (
                <Grid
                  item={{ area: 'stepIndicator' }}
                  as={VerticalFormProgress}
                  steps={steps}
                  currentStepId={currentStepId}
                />
              )}
              <VisuallyHiddenText aria-live='polite'>
                {previousId !== undefined && previousId !== currentStepId
                  ? t('step_changed_to_name', [currentStep.name])
                  : ''}
              </VisuallyHiddenText>
            </Grid>
          )}

          {currentStep.banners && <Grid item={{ area: 'banners' }}>{currentStep.banners}</Grid>}

          {currentStep.description && (
            <Grid item={{ area: 'description' }}>
              <HTML id={descriptionId} as='p' content={currentStep.description} />
            </Grid>
          )}

          <Grid item={{ area: 'content' }} as={StyledFormContent} ref={multiStepFormContentRef}>
            {currentStep.content}
          </Grid>

          <Progress
            visible={!!progress}
            focusOnVisible
            placement='local'
            message={typeof progress === 'string' ? progress : undefined}
          />
        </Grid>
        {currentStep.actions && (
          <Flex container={{ justify: 'between', alignItems: 'center' }}>
            {currentStep.actions}
          </Flex>
        )}
      </StyledForm>
    );
  }
);

export default MultiStepForm;
