import { FC, Ref, forwardRef, useState, useEffect } from 'react';

import { HTML, ForwardProps, ErrorState, useI18n } from '@pega/cosmos-react-core';

import convertMarkdown from './utils/markdownConverter';
import { RichTextViewerProps } from './RichTextViewer.types';
import { StyledEditorContainer, StyledRichTextViewer } from './RichTextEditor.styles';
import { convertHtml } from './utils/htmlConverter';
import { RichTextTreeRenderer, RichTextTreeRendererProps } from './RichTextTreeRenderer';

// Regex to match valid URLs
export const URL_REGEX =
  /(?<leadingChar>^|\s)(?<url>(?<protocol>https?:\/\/)?(?<host>[a-z0-9]+(?:[-.][a-z0-9]+)*\.(?<tld>[a-z]{2,5})(?<port>:[0-9]{1,5})?)(?<pathQueryHash>[/a-zA-Z0-9?=&+%@.|#_~:[\]'()-]*)?)/g;

const RichTextViewer: FC<RichTextViewerProps & ForwardProps> = forwardRef(function RichTextViewer(
  { content, type, interactionRenderers, markdownMap, onRetry, ...restProps }: RichTextViewerProps,
  ref: Ref<HTMLDivElement>
) {
  const t = useI18n();
  const [nodes, setNodes] = useState<RichTextTreeRendererProps['nodes']>([]);
  const [error, setError] = useState(false);

  useEffect(() => {
    if (type === 'markdown') {
      setNodes(
        convertMarkdown(
          content,
          [...(interactionRenderers || []), { regexPattern: URL_REGEX, type: 'builtin-url' }],
          markdownMap
        )
      );
    }
    if (type === 'richtext') {
      setNodes(JSON.parse(content));
    }
    if (type === 'html' && interactionRenderers && interactionRenderers.length > 0) {
      try {
        setNodes(convertHtml(content, interactionRenderers));
        setError(false);
      } catch {
        setNodes([]);
        setError(true);
      }
    }
  }, [content, type, interactionRenderers]);

  if (type === 'html' && (!interactionRenderers || interactionRenderers.length === 0)) {
    return <HTML ref={ref} content={content} />;
  }

  return error ? (
    <ErrorState message={t('rte_invalid_html')} onRetry={onRetry} />
  ) : (
    <StyledRichTextViewer ref={ref} {...restProps}>
      <StyledEditorContainer>
        <RichTextTreeRenderer nodes={nodes} />
      </StyledEditorContainer>
    </StyledRichTextViewer>
  );
});

export default RichTextViewer;
