import { RenderElementProps, RenderLeafProps } from 'slate-react';
import { CosmosCustom } from 'slate';
import { ComponentType, ReactNode } from 'react';
import styled from 'styled-components';

import { Text, Link, Image } from '@pega/cosmos-react-core';

import { Table, TableRow, TableCell, TableBody } from '../DecoratorComponents/Table';

// Renderer for block level elements
export const slateNodeRenderer = (props: RenderElementProps) => {
  switch (props.element.type) {
    case 'heading-1':
      return (
        <Text variant='h1' {...props.attributes}>
          {props.children}
        </Text>
      );
    case 'heading-2':
      return (
        <Text variant='h2' {...props.attributes}>
          {props.children}
        </Text>
      );
    case 'heading-3':
      return (
        <Text variant='h3' {...props.attributes}>
          {props.children}
        </Text>
      );
    case 'heading-4':
      return (
        <Text variant='h4' {...props.attributes}>
          {props.children}
        </Text>
      );
    case 'ordered-list':
      return <ol {...props.attributes}>{props.children}</ol>;
    case 'unordered-list':
      return <ul {...props.attributes}>{props.children}</ul>;
    case 'list-item':
      return (
        <li
          {...props.attributes}
          data-order={props.element.order}
          data-level={props.element.level}
          data-id={props.element.id}
          data-parent-id={props.element.parentId}
        >
          {props.children}
        </li>
      );
    case 'custom': {
      const CustomComponent = props.element.component;
      let child = <CustomComponent {...props.attributes}>{props.element.content}</CustomComponent>;
      if (props.element.href) {
        child = <Link href={props.element.href}>{child}</Link>;
      }
      if (props.element.italic) {
        child = <em>{child}</em>;
      }
      if (props.element.bold) {
        child = <strong>{child}</strong>;
      }
      if (props.element['line-through']) {
        child = <del>{child}</del>;
      }
      return child;
    }
    case 'override': {
      return props.element.component;
    }
    case 'table':
      return <Table {...props.attributes}>{props.children}</Table>;
    case 'table-body':
      return <TableBody {...props.attributes}>{props.children}</TableBody>;
    case 'table-row':
      return <TableRow {...props.attributes}>{props.children}</TableRow>;
    case 'table-cell':
      return <TableCell {...props.attributes}>{props.children}</TableCell>;
    case 'paragraph':
      return (
        <div>
          <Text {...props.attributes}>{props.children}</Text>
        </div>
      );
    case 'image':
      return (
        <div {...props.attributes}>
          <Image src={props.element.src} alt={props.element.alt as string} />
          {props.children}
        </div>
      );
    case 'image-placeholder':
      return <div {...props.attributes}>{props.children}</div>;
    default: {
      return <span {...props.attributes}>{props.children}</span>;
    }
  }
};

type Node = {
  children: JSX.Element[];
  type: string;
  href?: string;
  italic?: boolean;
  bold?: boolean;
  'line-through'?: boolean;
  component?: JSX.Element | ComponentType<any>;
  src?: string;
  alt?: string;
  content?: Record<string, string>;
};

// Renderer for block level elements
export const nodeRenderer = (node: Node) => {
  switch (node.type) {
    case 'heading-1':
      return <Text variant='h1'>{node.children}</Text>;
    case 'heading-2':
      return <Text variant='h2'>{node.children}</Text>;
    case 'heading-3':
      return <Text variant='h3'>{node.children}</Text>;
    case 'heading-4':
      return <Text variant='h4'>{node.children}</Text>;
    case 'ordered-list':
      return <ol>{node.children}</ol>;
    case 'unordered-list':
      return <ul>{node.children}</ul>;
    case 'list-item':
      return <li>{node.children}</li>;
    case 'custom': {
      const CustomComponent = node.component as ComponentType<any>;
      let child = <CustomComponent>{node.content}</CustomComponent>;
      if (node.href) {
        child = <Link href={node.href}>{child}</Link>;
      }
      if (node.italic) {
        child = <em>{child}</em>;
      }
      if (node.bold) {
        child = <strong>{child}</strong>;
      }
      if (node['line-through']) {
        child = <del>{child}</del>;
      }
      return child;
    }
    case 'override': {
      return node.component as JSX.Element;
    }
    case 'table':
      return <Table>{node.children}</Table>;
    case 'table-body':
      return <TableBody>{node.children}</TableBody>;
    case 'table-row':
      return <TableRow>{node.children}</TableRow>;
    case 'table-cell':
      return <TableCell>{node.children}</TableCell>;
    case 'paragraph':
      return (
        <div>
          <Text>{node.children}</Text>
        </div>
      );
    case 'image':
      return (
        <div>
          <Image src={node.src} alt={node.alt as string} />
          {node.children}
        </div>
      );
    case 'image-placeholder':
      return <div>{node.children}</div>;
    case 'space':
      return <br />;
    default: {
      return <>{node.children}</>;
    }
  }
};

// Renderer for inline elements
export const slateLeafRenderer = (props: RenderLeafProps) => {
  let child = props.children;

  if (props.leaf.searchResult) {
    const CustomComponent = props.leaf.searchResult.component;
    return (
      <span {...props.attributes}>
        <CustomComponent {...props.leaf.searchResult.props}>{child}</CustomComponent>
      </span>
    );
  }

  if (props.leaf['line-through']) {
    child = <del>{child}</del>;
  }

  if (props.leaf.italic) {
    child = <em>{child}</em>;
  }

  if (props.leaf.bold) {
    child = <strong>{child}</strong>;
  }

  if (props.leaf.href) {
    child = <Link href={props.leaf.href}>{child}</Link>;
  }

  return <span {...props.attributes}>{child}</span>;
};

const StyledSpan = styled.span`
  white-space: pre-line;
`;

export const leafRenderer = (leaf: CosmosCustom.Text) => {
  let child: string | ReactNode = leaf.text;
  if (leaf.searchResult) {
    const CustomComponent = leaf.searchResult.component;
    return (
      <span>
        <CustomComponent {...leaf.searchResult.props}>{child}</CustomComponent>
      </span>
    );
  }

  if (leaf['line-through']) {
    child = <del>{child}</del>;
  }

  if (leaf.italic) {
    child = <em>{child}</em>;
  }

  if (leaf.bold) {
    child = <strong>{child}</strong>;
  }

  if (leaf.href) {
    child = <Link href={leaf.href}>{child}</Link>;
  }

  return <StyledSpan>{child}</StyledSpan>;
};
