import { FC, forwardRef, PropsWithoutRef } from 'react';

import { ForwardProps } from '../../types';

import { StyledProgressEllipsis } from './Progress.styles';
import { ProgressProps } from './Progress.types';

const Ellipsis: FC<ProgressProps & ForwardProps> = forwardRef(function Ellipsis(
  { message, placement, ...restProps }: PropsWithoutRef<ProgressProps>,
  ref: ProgressProps['ref']
) {
  return (
    <StyledProgressEllipsis ref={ref} role='progressbar' placement={placement} {...restProps}>
      <span>
        <span />
        <span />
        <span />
      </span>
    </StyledProgressEllipsis>
  );
});

export default Ellipsis;
