import { CosmosCustom, Element, Text } from 'slate';
import { marked } from 'marked';

import { hasProp } from '@pega/cosmos-react-core';

import { InteractionRenderers, RichTextViewerProps } from '../RichTextViewer.types';

import parseTextToCustomSlateNodes from './interactionRenderer';

marked.setOptions({ mangle: false });

const getNode = (
  token: marked.Token,
  interactionRenderers?: InteractionRenderers[],
  markdownMap?: RichTextViewerProps['markdownMap']
): Element | Text => {
  if (!hasProp(token, 'type') || token.type === 'def')
    return { type: 'paragraph', children: [{ text: '' }] };

  let children;

  if (hasProp(token, 'tokens')) {
    // eslint-disable-next-line @typescript-eslint/no-use-before-define
    children = markDownToSlate(
      (token.tokens as marked.Tokens.Link['tokens']) || [],
      interactionRenderers,
      markdownMap
    );
  } else if (hasProp(token, 'items')) {
    // eslint-disable-next-line @typescript-eslint/no-use-before-define
    children = markDownToSlate(token.items, interactionRenderers, markdownMap);
  } else {
    let mdText = '';

    if (token.type === 'text') mdText = token.raw;
    else if (hasProp(token, 'text')) mdText = token.text;

    children = parseTextToCustomSlateNodes(mdText, interactionRenderers);
  }

  if (markdownMap?.[token.type]) {
    // Typed as any since typescript is unaware that token type matches argument
    const CustomComponent: any = markdownMap[token.type]?.(token as any);
    if (CustomComponent)
      return {
        type: 'override',
        component: <CustomComponent token={token} />,
        children: [{ text: '' }]
      };
  }

  if (!children.length) {
    return {
      type: 'paragraph',
      children: [{ text: token.raw }]
    };
  }

  switch (token.type) {
    case 'paragraph': {
      return {
        type: token.type,
        children
      };
    }
    case 'heading': {
      return {
        type:
          token.depth && token.depth <= 4
            ? (`heading-${token.depth}` as `heading-${1 | 2 | 3 | 4}`)
            : 'heading-4',
        children
      };
    }
    case 'link': {
      return {
        href: token.href,
        text: token.text || ''
      };
    }
    case 'em': {
      return {
        italic: true,
        text: token.text || ''
      };
    }
    case 'strong': {
      return {
        bold: true,
        text: token.text || ''
      };
    }
    case 'del': {
      return {
        'line-through': true,
        text: token.text || ''
      };
    }
    case 'list': {
      return {
        type: token.ordered ? 'ordered-list' : 'unordered-list',
        children: children as CosmosCustom.ListItemElement[]
      };
    }
    case 'list_item': {
      return {
        type: 'list-item',
        children: children as Element[]
      };
    }
    case 'image': {
      return {
        type: 'image',
        alt: token.text,
        src: token.href,
        children: [{ text: '' }]
      };
    }
    default: {
      return {
        type: token.type,
        children
      } as Element;
    }
  }
};

const markDownToSlate = (
  tokens: marked.Token[],
  interactionRenderers?: InteractionRenderers[],
  markdownMap?: RichTextViewerProps['markdownMap']
): (Element | Text)[] => {
  return tokens.map(token => {
    return getNode(token, interactionRenderers, markdownMap);
  });
};

export const convertMarkdownForEditor = (
  md = '',
  searchRenderers?: InteractionRenderers[]
): (Element | Text)[] => {
  return [
    {
      type: 'paragraph',
      children: parseTextToCustomSlateNodes(md, searchRenderers)
    }
  ];
};

// Converts markdown into Slate compatible nodes, also takes regex used
// in Feed Component to pass them down to custom elements
const convertMarkdown = (
  md: string,
  interactionRenderers?: InteractionRenderers[],
  markdownMap?: RichTextViewerProps['markdownMap']
): (Element | Text)[] => {
  const lexer = new marked.Lexer({ gfm: false });
  const output = lexer.lex(md);
  return markDownToSlate(output, interactionRenderers, markdownMap);
};

export default convertMarkdown;
