// eslint-disable-next-line import/prefer-default-export
export const followingNodes = <T extends Node>(source: NodeIterator, startingNode: Node): T[] => {
  const prevNodes = [];
  source.nextNode();
  while (source.referenceNode !== null && source.referenceNode !== startingNode) {
    prevNodes.push(source.referenceNode);
    source.nextNode();
  }

  const nodes = [];
  while (source.nextNode() !== null) {
    nodes.push(source.referenceNode);
  }

  return nodes.concat(prevNodes) as T[];
};
