import {
  forwardRef,
  FunctionComponent,
  PropsWithoutRef,
  Ref,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import styled from 'styled-components';

import Icon, { registerIcon } from '../Icon';
import { BaseProps, ForwardProps } from '../../types';
import HTML from '../HTML';
import Button from '../Button';
import { DialogHandleValue, InfoDialog } from '../Dialog';
import * as informationIcon from '../Icon/icons/information.icon';
import { useConsolidatedRef, useI18n } from '../../hooks';
import Flex from '../Flex';

registerIcon(informationIcon);

export interface AdditionalInfoProps extends BaseProps {
  heading: string;
  children: string | JSX.Element;
  dialogHandle?: Ref<DialogHandleValue>;
  ref?: Ref<HTMLDivElement>;
}

export const StyledAdditionalInfo = styled.div``;

const AdditionalInfo: FunctionComponent<AdditionalInfoProps & ForwardProps> = forwardRef(
  function AdditionalInfo(
    { heading, children, dialogHandle, ...restProps }: PropsWithoutRef<AdditionalInfoProps>,
    ref: AdditionalInfoProps['ref']
  ) {
    const t = useI18n();
    const [target, setTarget] = useState<HTMLButtonElement | null>(null);
    const buttonRef = useRef<HTMLButtonElement>(null);
    const handleRef = useConsolidatedRef(dialogHandle);

    useImperativeHandle(handleRef, () => {
      return {
        open: () => {
          setTarget(buttonRef.current);
        },
        close: () => {
          setTarget(null);
        }
      };
    });

    return (
      <Flex container as={StyledAdditionalInfo} ref={ref} {...restProps}>
        <Button
          variant='simple'
          label={t('additional_info')}
          compact
          icon
          ref={buttonRef}
          onClick={() => {
            handleRef.current?.open();
          }}
          aria-label={`${t('additional_info')} - ${heading}`}
        >
          <Icon name='information' />
        </Button>
        {target && (
          <InfoDialog
            heading={heading}
            target={target}
            onDismiss={() => {
              handleRef.current?.close();
            }}
          >
            {typeof children === 'string' ? <HTML content={children} /> : children}
          </InfoDialog>
        )}
      </Flex>
    );
  }
);

export default AdditionalInfo;
