import { forwardRef, FunctionComponent, Ref, ElementType, useContext, useEffect } from 'react';

import Flex from '../Flex';
import Image from '../Image';
import SearchInput from '../SearchInput';
import { useBreakpoint, useElement } from '../../hooks';
import { ForwardProps } from '../../types';
import BareButton from '../Button/BareButton';
import VisuallyHiddenText from '../VisuallyHiddenText';

import { AppHeaderProps } from './AppShell.types';
import AppShellOperator from './Operator';
import ContextSwitcher from './ContextSwitcher';
import {
  StyledAppHeader,
  StyledAppHeaderInfo,
  StyledAppHeaderSearchForm,
  StyledAppHeaderSpacer,
  StyledAppHeaderText,
  StyledAppHeaderOperator
} from './AppHeader.styles';
import AppShellContext from './AppShellContext';

const AppHeader: FunctionComponent<AppHeaderProps & ForwardProps> = forwardRef(function AppHeader(
  {
    appName,
    contextSwitcher: { contexts, onContextClick, label: contextSwitcherLabel } = {},
    fullImageSrc,
    imageSrc,
    href,
    onClick,
    portalName,
    target,
    appNameHidden,
    operator,
    searchInput,
    searchLabel,
    searchPage
  }: AppHeaderProps,
  ref: Ref<Element>
) {
  const isMediumOrAbove = useBreakpoint('md');
  const [searchContainer, setSearchContainer] = useElement<HTMLElement>();
  const { drawerOpen, setSearchContainerEl } = useContext(AppShellContext);

  useEffect(() => {
    if (searchPage && searchContainer) {
      setSearchContainerEl(searchContainer);
    } else {
      setSearchContainerEl(null);
    }
  }, [searchPage, searchContainer, setSearchContainerEl]);

  let appInfoAs: ElementType = 'div';
  if (href) appInfoAs = 'a';
  else if (onClick) appInfoAs = BareButton;

  return (
    <Flex
      as={StyledAppHeader}
      container={{ alignItems: 'center', pad: 1 }}
      hideTitle={!isMediumOrAbove}
      drawerOpen={drawerOpen}
      ref={ref}
    >
      {contexts && contexts.length > 1 && onContextClick && (
        <ContextSwitcher
          contexts={contexts}
          onContextClick={onContextClick}
          label={contextSwitcherLabel}
        />
      )}

      <StyledAppHeaderInfo centerLogo={!fullImageSrc}>
        <Flex
          container={{ inline: true, alignItems: 'center', gap: 1 }}
          as={appInfoAs}
          href={href}
          onClick={onClick}
          target={target}
        >
          <Image src={fullImageSrc || imageSrc} alt='' aria-hidden='true' />
          {isMediumOrAbove && (
            <>
              <StyledAppHeaderText
                as={appNameHidden ? VisuallyHiddenText : 'span'}
                variant='primary'
              >
                {appName}
              </StyledAppHeaderText>
              {portalName && (
                <StyledAppHeaderText
                  as={appNameHidden ? VisuallyHiddenText : 'span'}
                  variant='secondary'
                >
                  {portalName}
                </StyledAppHeaderText>
              )}
            </>
          )}
        </Flex>
      </StyledAppHeaderInfo>
      <Flex item={{ grow: 1 }} container={{ justify: 'end' }}>
        {searchInput ? (
          <StyledAppHeaderSearchForm
            ref={setSearchContainer}
            role='search'
            aria-label={searchLabel}
            onSubmit={e => e.preventDefault()}
            isMediumOrAbove={isMediumOrAbove}
          >
            {!searchPage && <SearchInput {...searchInput} />}
          </StyledAppHeaderSearchForm>
        ) : (
          <StyledAppHeaderSpacer />
        )}
      </Flex>

      <AppShellOperator
        as={StyledAppHeaderOperator}
        actions={operator.actions}
        popover={{ placement: 'bottom-start' }}
      >
        {operator.avatar}
      </AppShellOperator>
    </Flex>
  );
});

export default AppHeader;
